.dialogs {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
}

.dialogs__dialog {
    display: flex;
    justify-content: flex-start;
    width: 80%;
    margin-bottom: 20px;
}

.dialogItem {
    width: 15%;
    min-height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: start;
}

.messages {
    width: 80%;
    height: 100%;
    min-height: 150px;
    margin-left: 15px;
}

@media screen and (max-width: 600px) {

    .dialogItem {
        display: none
    }

    .dialogs__dialog {
        width: 100%;
    }

    .messages {
        width: 100%;
        margin-left: 5px;
    }

}


@media screen and (max-width: 320px) {
    .dialogs {
        width: 100%;
    }


    .dialogItem {
        display: none
    }

    .messages {
        width: 100%;
        margin-left: 5px;
    }

}